import parse from "date-fns/parse"
import { differenceInWeeks } from "date-fns"

const IsStorageOutdatedService = (storageVal: string): boolean => {
  const lastUpdate = storageVal.substr(storageVal.indexOf('-lastUpdate==') + 13)
  return differenceInWeeks(new Date(), parse(lastUpdate, 'dd/MM/yyyy', new Date())) >= 1
}

export default IsStorageOutdatedService